@import url(https://fonts.googleapis.com/css?family=Roboto:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navItem {
  color: white;
  text-transform: uppercase;
  /* padding-right: 0px; */
  margin-right: 50px;
}

.navItem:hover {
  text-decoration: none;
  color: #ff5722;
  text-transform: uppercase;
}

.cards{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:0.5em;gap:0.5em}.title{font-family:"Roboto";margin-top:1em;text-align:center;text-transform:uppercase}

.card-box{text-align:center;margin:2em;font-family:"Roboto";padding-bottom:0px}.card-box .red{background-color:#ff5722}.card-box .green{background:#4caf50}.card-box .yellow{background:#616161}.card-box .title{text-transform:uppercase;padding:0.6em;color:white}.card-box .status{background-color:#4b4b4b;text-transform:uppercase;padding:0.4em;color:white}

.card{margin:1em;text-align:center;font-family:"Roboto";padding:1em}.card .infected{background-color:#ff5722;color:white;text-transform:uppercase;letter-spacing:normal;margin-bottom:5px}.card .recovered{background-color:#4caf50;color:white;text-transform:uppercase;letter-spacing:normal;margin-bottom:5px}.card .deaths{background-color:#bdbdbd;color:white;text-transform:uppercase;letter-spacing:normal;margin-bottom:5px}

